/* NAVBAR CONTAINER */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  background: white;
  border-bottom: 1px solid #e6e6e6;
  position: sticky;
  top: 0;
  z-index: 1000;
}

/* LOGO */
.logo a {
  font-size: 26px;
  font-weight: 700;
  color: black;
  text-decoration: none;
}

/* NAV LINKS */
.nav-links {
  display: flex;
  align-items: center;
  gap: 20px;
}

.nav-links a {
  text-decoration: none;
  color: black;
  font-weight: 500;
  transition: 0.2s;
}

.nav-links a:hover {
  color: #666;
}

/* PROFILE MENU */
.profile-menu {
  position: relative;
  display: inline-block;
}

.username {
  cursor: pointer;
  font-weight: 600;
  padding: 6px 10px;
  border-radius: 6px;
  transition: 0.2s;
}

.username:hover {
  background: #f4f4f4;
}

/* DROPDOWN MENU */
.dropdown {
  position: absolute;
  top: 38px;
  right: 0;
  background: white;
  width: 170px;
  border-radius: 8px;
  padding: 10px 0;
  box-shadow: 0px 4px 12px rgba(0,0,0,0.15);
  animation: fadeIn 0.2s ease-in-out;
  z-index: 999;
}

.dropdown-item {
  display: block;
  padding: 12px 16px;
  text-decoration: none;
  font-size: 14px;
  color: black;
  transition: 0.2s;
}

.dropdown-item:hover {
  background: #f2f2f2;
}

/* LOGOUT BUTTON */
.logout-btn {
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  padding: 12px 16px;
  font-size: 14px;
  color: red;
  cursor: pointer;
}

.logout-btn:hover {
  background: #ffe6e6;
}

/* ANIMATION */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}

