/* üñ§ REWEAR ‚Äî MODERN WHITE THEME WITH BLACK TEXT AND POLISHED DESIGN */

/* UNIVERSAL RESET */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #ffffff;
  color: #111111;
  min-height: 100vh;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: #111111;
  transition: all 0.3s ease;
}

a:hover {
  opacity: 0.7;
}

.page-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 20px;
}

/* üè† HERO SECTION - Modern, Balanced & Aesthetic */
.hero-section {
  position: relative;
  text-align: center;
  padding: 90px 20px 100px;
  color: #ffffff;
  border-bottom: 1px solid #eee;
  background-image: url("https://images.unsplash.com/photo-1521572163474-6864f9cf17ab?w=1600");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  border-radius: 0 0 25px 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  overflow: hidden;
}

/* Soft dark gradient overlay for readability */
.hero-section::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.6) 0%,
    rgba(0, 0, 0, 0.55) 40%,
    rgba(0, 0, 0, 0.4) 100%
  );
  border-radius: 0 0 25px 25px;
  z-index: 1;
}

/* Hero Heading */
.hero-section h1 {
  position: relative;
  z-index: 2;
  font-size: 3rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-shadow: 0 3px 8px rgba(0, 0, 0, 0.4);
  animation: slideUp 1.2s ease-out forwards;
}

/* Hero Tagline */
.tagline {
  position: relative;
  z-index: 2;
  font-size: 1.3rem;
  font-weight: 400;
  color: #f5f5f5;
  letter-spacing: 0.5px;
  text-shadow: 0 3px 6px rgba(0, 0, 0, 0.4);
  opacity: 0;
  animation: fadeIn 1.4s ease 0.4s forwards;
}

/* Animated underline accent under tagline */
.tagline::after {
  content: "";
  display: block;
  margin: 15px auto 0;
  width: 80px;
  height: 2px;
  background: #ffffff;
  border-radius: 5px;
  transform: scaleX(0);
  animation: underlineGrow 1s ease 0.8s forwards;
}

/* Animations */
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes underlineGrow {
  to {
    transform: scaleX(1);
  }
}

/* üõç ACTION BAR - Buy/Swap | Sell */
.action-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  padding: 14px 40px;
  margin: 30px auto 40px;
  width: 55%;
  border-radius: 40px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.action-bar:hover {
  transform: scale(1.02);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.action-bar a {
  color: #000;
  font-weight: 500;
  font-size: 1.1rem;
  letter-spacing: 0.3px;
  transition: all 0.3s ease;
}

.action-bar a:hover {
  opacity: 0.7;
  transform: scale(1.05);
}

/* üßæ SECTION TITLE */
.section-title {
  text-align: center;
  color: #111;
  font-size: 1.9rem;
  margin: 50px 0 30px;
  font-weight: 600;
}

/* üëï PRODUCT GRID */
.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 35px;
  padding: 0 20px 60px;
  animation: fadeIn 1s ease forwards;
}

/* üëú PRODUCT CARD */
.product-card {
  background: #ffffff;
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.08);
  text-align: center;
  transition: all 0.4s ease;
  cursor: pointer;
}

.product-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.product-img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  transition: all 0.3s ease;
}

.product-card:hover .product-img {
  filter: brightness(85%);
}

.product-info {
  padding: 20px;
}

.product-info h3 {
  color: #111;
  font-weight: 500;
  margin-bottom: 8px;
}

.product-info p {
  color: #555;
}

.price {
  color: #000;
  font-weight: 600;
  margin: 10px 0;
}

.buy-btn {
  background: #000;
  color: #fff;
  padding: 10px 22px;
  border-radius: 6px;
  border: none;
  transition: all 0.3s ease;
  font-weight: 500;
}

.buy-btn:hover {
  background: #333;
  transform: scale(1.05);
}

/* üß• PRODUCT DETAIL PAGE */
.product-detail {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 50px;
  padding: 80px 40px;
  background-color: #ffffff;
}

.product-image-section {
  flex: 1;
  min-width: 300px;
  max-width: 450px;
  text-align: center;
}

.detail-img {
  width: 100%;
  border-radius: 12px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.detail-img:hover {
  transform: scale(1.03);
}

.product-detail-info {
  flex: 1;
  min-width: 300px;
  max-width: 500px;
}

.product-detail-info h1 {
  font-size: 2rem;
  color: #111;
  margin-bottom: 20px;
}

.detail-desc {
  color: #444;
  line-height: 1.6;
  margin-bottom: 20px;
}

.detail-price {
  font-size: 1.5rem;
  color: #000;
  font-weight: 600;
  margin: 25px 0;
}

.sizes {
  margin: 25px 0;
}

.size-options {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.size-btn {
  background: #fff;
  border: 1px solid #ccc;
  color: #111;
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.size-btn:hover {
  background: #000;
  color: white;
  border-color: #000;
  transform: scale(1.05);
}

.product-actions {
  display: flex;
  gap: 15px;
  margin-top: 30px;
}

.swap-btn {
  background: transparent;
  color: #000;
  border: 2px solid #000;
  border-radius: 6px;
  padding: 10px 20px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.swap-btn:hover {
  background: #000;
  color: #fff;
  transform: scale(1.05);
}

/* üßæ FORM PAGES */
.form-container {
  background: #fafafa;
  border-radius: 14px;
  max-width: 420px;
  margin: 120px auto;
  padding: 40px 30px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
  text-align: center;
}

.form-container h2 {
  color: #111;
  font-weight: 600;
  margin-bottom: 25px;
}

.form-group {
  margin-bottom: 18px;
  text-align: left;
}

.form-group label {
  display: block;
  color: #333;
  margin-bottom: 6px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px;
  background: #ffffff;
  border: 1px solid #ccc;
  border-radius: 8px;
  color: #111;
  font-size: 0.95rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: 2px solid #000;
  transform: scale(1.01);
}

.form-btn {
  width: 100%;
  background: #000;
  color: #ffffff;
  font-size: 1rem;
  padding: 12px;
  border-radius: 8px;
  transition: all 0.3s ease;
  font-weight: 500;
}

.form-btn:hover {
  background: #333;
  transform: scale(1.03);
}

.form-footer {
  margin-top: 15px;
  color: #555;
}

/* üßæ ABOUT PAGE */
.about-page {
  text-align: center;
  max-width: 800px;
  margin: 100px auto;
  background: #fafafa;
  padding: 40px 50px;
  border-radius: 16px;
