}

/* BEAUTIFUL CARD STYLING */
.form-card {
  width: 350px;
  background: #ffffff;
  padding: 30px;
  border-radius: 14px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.15);
  animation: fadeInUp 0.4s ease;
}

/* INPUT STYLING */
.form-card input,
.form-card textarea,
.form-card select {
  width: 100%;
  padding: 12px;
  margin-top: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 15px;
  transition: 0.2s ease;
  outline: none;
}

.form-card input:focus,
.form-card textarea:focus,
.form-card select:focus {
  border-color: #333;
}

/* BUTTON */
.form-card button {
  width: 100%;
  padding: 12px;
  margin-top: 18px;
  background: black;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: 0.2s ease;
}

.form-card button:hover {
  transform: translateY(-2px);
  background: #222;
}

/* TITLE */
.form-card h2 {
  text-align: center;
  margin-bottom: 15px;
  font-size: 24px;
  font-weight: 700;
}

/* ANIMATION */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}

.product-card img {
  width: 100%;
  height: 700px;          /* bigger image */
